body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f0f0f0;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.file-inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.file-input {
  flex: 1;
  margin-right: 10px;
}

.file-input:last-child {
  margin-right: 0;
}

.file-input input[type="file"] {
  display: none;
}

.file-input label {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
}

.content {
  display: flex;
  justify-content: space-between;
}

.file-container {
  flex: 1;
  margin-right: 20px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 5px;
}

.file-container:last-child {
  margin-right: 0;
}

.file-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.change-counter {
  font-size: 14px;
  color: #666;
}

.tree-container, .table-container {
  margin-bottom: 20px;
}

.virtual-scroll-content {
  height: 300px;
  overflow-y: auto;
  border: 1px solid #ddd;
  padding: 10px;
  background-color: #fff;
}

button {
  padding: 10px 20px;
  background-color: #28a745;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #218838;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}